import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AuthGuard } from './auth/auth.guard';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full'
  },
  {
    path: 'home',
    canActivate: [AuthGuard],
    loadChildren: './home/home.module#HomePageModule'
  },
  {
    path: 'login',
    // canActivate: [AuthGuard],
    loadChildren: './auth/login/login.module#LoginPageModule'
  },
  {
    path: 'recovery',
    // canActivate: [AuthGuard],
    loadChildren: './auth/recovery/recovery.module#RecoveryPageModule'
  },
  {
    path: 'misdatos',
    canActivate: [AuthGuard],
    loadChildren: './misdatos/misdatos.module#MisdatosPageModule'
  },
  {
    path: 'cartola',
    canActivate: [AuthGuard],
    loadChildren: './cartola/cartola.module#CartolaPageModule'
  },
  {
    path: 'facturaspendientes',
    canActivate: [AuthGuard],
    loadChildren: './facturaspendientes/facturaspendientes.module#FacturaspendientesPageModule'
  },
  {
    path: 'facturaspagadas',
    canActivate: [AuthGuard],
    loadChildren: './facturaspagadas/facturaspagadas.module#FacturaspagadasPageModule'
  }

];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
